    font-size: 14px;
  }
  &__nav__link{
    display: inline-block;
  }
}

.l-menu{
  // padding-right: 10px;
  &--link a{
    color: $primaryColor;
    text-decoration: none;
    // padding: 13px 10px;
    border-bottom-width: 3px;
    border-bottom-style: solid;
    border-bottom-color: transparent;
    display: inline-block;
    &:hover{
      border-bottom-color: $primaryColor;
    }
  }
  &__item{
    // margin-right: 25px;
    // &:last-child{
    //   margin-right: 0px;
    // }
    &--active a {
      border-bottom-color: $primaryColor;
    }
  }
}


.l-communityList{
  a {
    text-decoration: none;
    &:hover {
      color: $blackColor;
    }
    // img {
    //   margin-right: 3px;
    // }
  }
  &--fb a {
    color: $facebookColor;
    // padding-right: 6px;
    // margin-right: 6px;
    border-right: 1px $GainsboroColor solid;
  }
  &--wt a {
    color: $twitterColor;
  }
