$spacCardinalNum: 1;
$spacUnit: px;
@for $i from 0 through 40 {
  .u-p{
    &-#{ ($i * $spacCardinalNum) } {
      padding:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &x-#{ ($i * $spacCardinalNum) } {
      padding-left:  ($i * $spacCardinalNum) + $spacUnit;
      padding-right:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &y-#{ ($i * $spacCardinalNum) } {
      padding-top:  ($i * $spacCardinalNum) + $spacUnit;
      padding-bottom:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &t-#{ ($i * $spacCardinalNum) } {
      padding-top:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &b-#{ ($i * $spacCardinalNum) } {
      padding-bottom:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &r-#{ ($i * $spacCardinalNum) } {
      padding-right:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &l-#{ ($i * $spacCardinalNum) } {
      padding-left:  ($i * $spacCardinalNum) + $spacUnit;
    }
  }
  .u-m{
    &-#{ ($i * $spacCardinalNum) } {
      margin:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &x-#{ ($i * $spacCardinalNum) } {
      margin-left:  ($i * $spacCardinalNum) + $spacUnit;
      margin-right:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &y-#{ ($i * $spacCardinalNum) } {
      margin-top:  ($i * $spacCardinalNum) + $spacUnit;
      margin-bottom:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &t-#{ ($i * $spacCardinalNum) } {
      margin-top:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &b-#{ ($i * $spacCardinalNum) } {
      margin-bottom:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &r-#{ ($i * $spacCardinalNum) } {
      margin-right:  ($i * $spacCardinalNum) + $spacUnit;
    }
    &l-#{ ($i * $spacCardinalNum) } {
      margin-left:  ($i * $spacCardinalNum) + $spacUnit;
    }
  }
}

// 較少用 margin 與 padding 不跑回圈獨立出來
.u-mb-62{
  margin-bottom: 62px;
}
.u-mb-64{
  margin-bottom: 64px;
}
.u-pb-50{
  padding-bottom: 50px;
}

.u-mx-auto{
  margin-left: auto;
  margin-right: auto;
}